import React from "react";
import {
  HashRouter as Router,
  Switch,
  Route,
  Redirect
} from "react-router-dom";
import Header from "./components/header/header";
import Footer from "./components/footer/footer"
import { Report } from "./containers/report/report";
import { Survay } from "./containers/survey/survay";
import { UserDetails } from "./containers/user-details/user-details";
import DataRoute from "./util/data-auth-route";
const getBasename = path => path.substr(0, path.lastIndexOf('/'));

function App() {
  return (
    <>
      <Header />
      <div className="container">
        <Router>
          <Switch>
            <Route exact path={`/`} render={(props) => <Redirect to={`/user-details`} />} />
            <Route exact path={`/user-details`} component={UserDetails} />
            <Route exact path={`/survey/:componentId/inner/:childComponentId`} render={(props) => <DataRoute component={Survay} {...props} />} />
            <Route exact path={`/report`} render={() => <DataRoute component={Report} />} />
            <Route render={(props) => <Redirect to="user-details" {...props} />} />
          </Switch>
        </Router>
      </div>
      <Footer />
    </>
  );
}

export default App;
